@use 'variables' as *;
@use 'mixins' as *;

$-padding: 1.5rem;

:host {
  display: block;
  margin-bottom: $cat-body-margin-bottom;
  border-radius: cat-border-radius('l');
  background-color: cat-token('color.ui.background.body');
  padding: $-padding;
}

:host([hidden]) {
  display: none;
}

::slotted(nav) {
  margin: #{$cat-body-margin-bottom * 0.5 - $-padding} #{-$-padding} !important;
}

::slotted(:last-child) {
  margin-bottom: 0;
}

::slotted(.cat-card-pull) { margin: -$-padding !important; width: calc(100% + #{2 * $-padding}) !important; height: calc(100% + #{2 * $-padding}) !important; }
::slotted(.cat-card-pull-h) { margin-left: -$-padding !important; margin-right: -$-padding !important; width: calc(100% + #{2 * $-padding}) !important; }
::slotted(.cat-card-pull-v) { margin-top: -$-padding !important; margin-bottom: -$-padding !important; height: calc(100% + #{2 * $-padding}) !important; }
::slotted(.cat-card-pull-t) { margin-top: -$-padding !important; }
::slotted(.cat-card-pull-l) { margin-left: -$-padding !important; }
::slotted(.cat-card-pull-r) { margin-right: -$-padding !important; }
::slotted(.cat-card-pull-b) { margin-bottom: -$-padding !important; }
